import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CanDeactivateGuard } from 'src/app/shared/guards/can-deactivate.guard';
import { AccountClosureMaintenanceSummaryComponent } from './account-closure-maintenance-summary/account-closure-maintenance-summary.component';
import { AccountClosureMaintenanceComponent } from './account-closure-maintenance/account-closure-maintenance.component';

const routes: Routes = [
  {
    path:"",
    component:AccountClosureMaintenanceComponent,
    canDeactivate: [CanDeactivateGuard]
  },
  {
    path:"account-closure-summary",
    component:AccountClosureMaintenanceSummaryComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AccountClosureMaintenanceRoutingModule { }
