import { Injectable } from '@angular/core';
import { Ng2IzitoastService } from 'ng2-izitoast';


@Injectable({
  providedIn: 'root'
})
export class ToastService {

  constructor(
    public iziToast: Ng2IzitoastService
  ) { }

  successMessage(title: string, msg: string) {
    this.iziToast.success({
      title: title,
      message: msg,
      layout: 2,
      balloon: false,
      position: "topRight",
      progressBarColor: "red",
      pauseOnHover: true,
    });
  }

  errorMessage(title: string, msg: string) {
    this.iziToast.error({
      title: title,
      message: msg,
      layout: 2,
      balloon: false,
      position: "topRight",
      progressBarColor: "red",
      pauseOnHover: true,
    });
  }

  infoMessage(title: string, msg: string) {
    this.iziToast.info({
      title: title,
      message: msg,
      layout: 2,
      balloon: false,
      position: "topRight",
      progressBarColor: "red",
      pauseOnHover: true,
    });
  }

}
